:root {
  --azules-fondo-oscuro-hex: #2d425c;
  --azules-blanco-hex: #c1d9f5;
  --azules-mas-tenue-hex: #6b9edb;
  --azules-fondo-hex: #212121;
  --azules-fondo-tenue-hex: #5279a8;
}

* {
  margin: 0;
  padding: 0;
  color: white;
}

body {
  background-color: #1e1e1e;
}
h1,
h3,
p,
div {
  background-color: transparent;
  text-align: center;
}

.bg-transparent {
  background-color: transparent;
}

.pag {
  height: 90vh;
  width: 100vw;
  background-color: var(--azules-fondo-oscuro-hex);
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
  scrollbar-width: none;
}

.title {
  margin-top: 5vh;
}
.tittle-abs {
  margin-top: 10vh;
}

.dark-tittle {
  color: var(--azules-fondo-oscuro-hex);
}

.blur {
  filter: blur(25px);
}

.close {
  text-align: right;
  position: absolute;
  right: 0;
  margin: 30px;
  cursor: pointer;
  z-index: 3;
}

.some-margin-bottom{
  margin-bottom: 1vh;
}

.nav-bar {
  width: 100%;
  display: flex;
}

.nav-bar-button {
  height: 10vh;
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.active-nav {
  background-color: var(--azules-fondo-oscuro-hex);
}
.no-active-nav {
  background-color: var(--azules-fondo-oscuro-hex);
  background-color: var(--azules-fondo-tenue-hex);
}
.no-active-nav:hover {
  background-color: var(--azules-fondo-oscuro-hex);
}

.collapse {
  margin-top: 5vh;
  margin-left: auto;
  margin-right: auto;
  max-width: 700px;
  cursor: pointer;
}

.decks-collapse {
  padding: 3vh;
  border-style: solid;
  border-width: 1px;
}

.decks-container {
  /* height: auto; */
  max-width: 690px;
  margin-left: auto;
  margin-right: auto;
  background-color: #c1d9f5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.card-container {
  /* height: 246px; */
  height: auto;
  min-width: 168px;
  margin: 10px;
  cursor: pointer;
}

.bg-overlay {
  /* display: inline-block; */
  position: absolute;
  height: 90vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(1, 1, 1, 0.9);
  cursor: auto;
  /* filter: blur(100px); */
}

.overlay {
  position: relative;
  height: 45vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 2;
  overflow-y: scroll;
  /* scrollbar-width: none; */
}

.deck-overlay {
  display: flex;
  max-width: 700px;
  margin-top: 5vh;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 5vh;
}

.img-s {
  max-width: 70px;
  margin: 5px;
  cursor: pointer;
}

.show-card {
  margin-top: 1vh;
  position: relative;
  height: 40vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 2;
  /* overflow-y: scroll; */
  /* background-color: aliceblue; */
}

.description {
  height: 100px;
  width: 300px;
  margin-top: 2vh;
  margin-left: auto;
  margin-right: auto;
  overflow-y: scroll;
}
